import React from "react";
import s from './Post.module.css';

const Post = (props) => {
    return(
        <div className={s.myPosts}>
                <div className={s.item}>
                    <img src='' />
                    {props.message}
                    <div>
                    likes {props.likesCount}
                    </div>
                </div>
        </div>
    )
}

export default Post;